@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);

* {
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  max-width: 1440px;
  margin: 0 auto;
}

.a {
    display: flex;
    align-items: center;
    margin: 200px 0;
}

.a-left {
    flex:1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-card.bg {
    position: absolute;
    top:50;
    left: 10;
    background: rgb(244, 244, 244);
}
.a-right {
    flex:1 1;
}

.a-card {
    width: 80%;
    height: 50vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.a-img {
    width: 100%;
    height: 100%;
    border-radius: 30px;
    object-fit: contain;
}

.a-title {
    font-weight: bold;
}

.a-sub {
    margin:20px 0;
    font-size: 1.2rem;
}

.a-desc {
    font-weight: 300;
    margin-right: 60px;
    color:rgb(135, 132, 132);
}

.a-stack {
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.a-stack-item {
    margin: 0 0.5em;
}
.award-texts {
    width: 70%;
}

.a-award-title {
    font-weight: bold;
    color: #13013fa5;
    margin-bottom: 10px;
}

@media screen and (max-width: 480px) {
    .a {
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    }

    .a-left {
        width: 100%;
    }

    .a-card {
        height: 30vh;
    }

    .a-card.bg {
        display: none;
    }

    .a-right {
        padding: 20px;
    }

    .a-stack {
        display: none;
    }

    .a-desc {
        margin-right: 0;
    }
}

.contact {
    height: 80vh;
    position: relative;
    display: flex;
    align-items: center;
}

.contact-bg {
    width: 20px;
    height: 100%;
    background-color: #13013fa5;
    position: absolute;
}

.contact-title {
    font-size: 60px;
    width: 80%;
}

.contact-wrapper {
    padding:50px;
    display: flex;
    align-items: center;
}

.contact-left {
    flex: 1 1;
}
.contact-right {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.contact-info-item {
    display: flex;
    align-items: center;
    margin: 50px 0;
    font-weight: 300;
    width: 70%;
}

.contact-icon {
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

.contact-description {
    font-weight: 200;
}

form {
    margin-top: 20px;

}

input {
    width: 50%;
    height: 50px;
    border:none;
    border-bottom: 1px solid #eee;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

textarea {
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

button {
    border: none;
    padding: 15px;
    background-color: #13013fa5;
    color: white;
    font-weight: 500;
    cursor: pointer;
}

@media screen and (max-width: 480px) {
    .contact-wrapper {
        flex-direction: column;
    }
    .contact-bg {
        height: 100%;

    }
}
.i {
    display: flex;
    height:100vh;
}

.i-left {
    flex:1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.i-left-wrapper{
    padding:50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.i-right {
    flex:1 1;
    position: relative;
}

.i-intro {
    font-size: 30px;
    font-weight: 300;
    margin: 0;
}

.i-name {
    font-size: 60px;
    margin-bottom: 40px;
}

.i-title {
    height: 50px;
    overflow: hidden;
    text-align: center;
    display: flex;
    justify-content: center;
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color:#472896;
    display: flex;
    align-items: center;
    text-align: center;
}

@keyframes move {
    25% {
        transform: translateY(-50px);
    }
    50% {
        transform: translateY(-90px);
    }
    75% {
        transform: translateY(-140px);
    }
    100% {
        transform: translateY(-190px);
    }
}

.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.coding-img {
    width: 100%;
    height: 100%;
}

.i-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.i-bg {
    -webkit-clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
            clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
    background-color: #eee;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}

@media screen and (max-width: 480px) {
    .i {
        flex-direction: column;
    }

    .i-left-wrapper {
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }

    .i-desc {
        display: none;
    }

    .i-title-wrapper {
        justify-content: center;
        text-align: center;
    }
    
    .i-title-item {
        text-align: center;
    }
}
.navbar {
  display: flex;
  position: fixed;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 1em 0;
  background-color: white;
  z-index: 1111;
}

.navbar .navbar-left h3 {
  font-size: 24px;
}

.navbar .navbar-left h3 .navbar-left-span {
  background: -webkit-linear-gradient(#72f, #13013fa5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.navbar nav {
  display: flex;
  align-items: center;
}

.navbar nav .nav-list {
  display: flex;
  list-style: none;
  justify-content: center;
}

.navbar nav .nav-list .nav-item {
  padding: 0 0 0 2em;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: .1rem;
  color: black;
  text-decoration: none;
  border: none;
}
